import {
    createSlice,
    PayloadAction,
} from '@reduxjs/toolkit';
import {RootState} from '../../app/store';
import {START_ID} from './utils';

interface QuizState {
    userRates: Array<number>,
    questionIndex: number,
    characterId: number | null,
    isFinish: boolean,
    usersAnswers: Array<number>
}

const initialState: QuizState = {
    questionIndex: START_ID,
    characterId: null,
    userRates: [],
    isFinish: false,
    usersAnswers: []
};


export const quizSlice = createSlice({
    name: 'quiz',
    initialState,
    // The `reducers` field lets us define reducers and generate associated actions
    reducers: {
        nextQuestion: (state, action: PayloadAction<number>) => {
          state.questionIndex = action.payload;
        },
        setCharacter: (state, action: PayloadAction<number | null>) => {
            state.characterId = action.payload
        },
        setNewRate: (state, action: PayloadAction<number>) =>{
            state.userRates = [...state.userRates, action.payload]
        },
        goToStart: () =>({...initialState}),
        goToResult: (state) =>{
            state.isFinish = true
        },
        saveAnswer: (state, action: PayloadAction<number>) =>{
            state.usersAnswers = [...state.usersAnswers, action.payload]
        },

    },
});

export const { nextQuestion, setCharacter, setNewRate, goToStart, goToResult, saveAnswer} = quizSlice.actions;

export const selectQuestionIndex = (state: RootState) => state.quiz.questionIndex;
export const selectUserRates= (state: RootState) => state.quiz.userRates;
export const selectCharacter = (state: RootState) => state.quiz.characterId;
export const selectFinish = (state: RootState) => state.quiz.isFinish;
export const selectAnswers = (state: RootState) => state.quiz.usersAnswers;

export default quizSlice.reducer;
